import React from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import htmr from 'htmr'

import {
  Typography
} from '@material-ui/core'

import Frame from 'components/frame'
import BlogList from 'components/blogList'
import BlogData from 'components/blogData'
import Container from 'components/container'

const styles = theme => ({
  root: {
    paddingBottom: 30,

    '& .headline': {
      position: 'relative',
      borderRadius: 3,
      marginBottom: 30,

      '& img': {
        width: '100%',
        borderRadius: 3
      },

      '& .overlay': {
        position: 'absolute',
        width: '100%',
        height: '70%',
        background: 'linear-gradient(0deg, #fff, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.01))',
        bottom: 0,
        zIndex: 0
      },

      '& .title': {
        position: 'absolute',
        bottom: 10,
        left: 30,
        fontSize: 36,
        fontWeight: 700,
        marginBottom: 15,
        lineHeight: 1.3,
        transition: 'color 0.3s ease',
        zIndex: 99,

        '&:hover': {
          color: theme.palette.primary.main
        }
      }
    },

    '& .box': {
      borderRadius: 3,
      backgroundColor: '#fff',
      boxShadow: '0 1px 3px 0 rgba(0, 0, 0, 0.16)',
      padding: '20px 50px'
    }
  }
})

class Index extends React.Component {
  render () {
    const { classes } = this.props

    const headline = BlogData && BlogData.filter(data => data.headline === true)[0]

    return (
      <Frame>
        <div className={classes.root}>
          <Container>
            <div className="headline">
              <img src={headline.image} alt={headline.title} />
              <div className="overlay" />
              <Typography className="title" variant="h2" gutterBottom>
                {htmr(headline.title)}
              </Typography>
            </div>
            <div className="box">
              {BlogData && BlogData.map((data, i) =>
                <BlogList
                  key={i}
                  data={data}
                />
              )}
            </div>
          </Container>
        </div>
      </Frame>
    )
  }
}

Index.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(Index)
